.calculator__top {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #2C2C2C;
  position: relative; }
  .calculator__top-title {
    position: absolute;
    top: -10px;
    left: 20px; }
  .calculator__top input[type="number"] {
    width: 50%; }

.calculator__middle {
  display: flex; }

.calculator__left {
  width: 60%; }

.calculator__right {
  width: 40%;
  padding-left: 20px; }

.calculator_result__title {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 19px;
  color: #D9CB9E; }

.calculator_result__main {
  border: 1px solid #D9CB9E;
  padding: 20px 30px;
  display: flex;
  flex-wrap: wrap;
  margin: 20px 0; }

.calculator_result__price {
  width: 50%; }
  .calculator_result__price-currency {
    margin-right: 20px; }
  .calculator_result__price-value {
    color: #D9CB9E; }

.calculator__bottom {
  display: flex;
  justify-content: space-between;
  align-items: center; }

.calculator__btn {
  display: flex; }
  .calculator__btn button {
    margin-right: 10px; }

.calculator_course {
  display: flex; }
  .calculator_course div:not(:last-child) {
    margin-right: 20px; }

@media (max-width: 1000px) {
  .calculator__middle {
    flex-direction: column; }
  .calculator__left,
  .calculator__right {
    width: 100%; }
  .calculator__bottom, .calculator__btn {
    flex-direction: column-reverse; }
    .calculator__bottom button, .calculator__bottom div, .calculator__btn button, .calculator__btn div {
      width: 100%; } }
