.title {
  display: flex;
  justify-content: space-between; }
  .title_1 {
    font-weight: 500;
    font-size: 24px;
    line-height: 28px;
    color: #FFFFFF;
    position: relative;
    padding-left: 50px;
    min-width: 30%; }
    .title_1::before {
      content: '';
      width: 32px;
      height: 1px;
      background: #D9CB9E;
      position: absolute;
      left: 0px;
      top: 15px; }
  .title_2 {
    font-weight: 500;
    font-size: 50px;
    line-height: 59px;
    color: #2C2C2C; }
    @media (max-width: 1000px) {
      .title_2 {
        display: none; } }

