main {
  background: linear-gradient(5.79deg, #010101 85.28%, #363A45 101.11%, #000000 101.11%);
  margin: 0 5%; }
  @media (max-width: 1000px) {
    main {
      margin: 0; } }

section {
  margin: 0 100px;
  padding-top: 0;
  padding: 40px; }
  @media (max-width: 1000px) {
    section {
      margin: 0; } }

.section_bg {
  background: rgba(255, 255, 255, 0.1);
  backdrop-filter: blur(2px); }
  .section_bg:not(:last-child) {
    margin-bottom: 20px; }

@media (max-width: 1000px) {
  .banner {
    padding-top: 60px; } }

.banner__slide {
  background-repeat: no-repeat;
  background-position: right;
  background-blend-mode: color; }

.banner__left {
  padding: 100px; }
  @media (max-width: 1000px) {
    .banner__left {
      padding: 5%; } }

.banner__title {
  font-style: normal;
  font-weight: 900;
  font-size: 43px;
  line-height: 50px; }
  .banner__title-1 {
    color: #FFFFFF; }
  .banner__title-2 {
    color: #D9CB9E; }

.banner__text {
  margin: 40px 0;
  color: #FFFFFF;
  font-weight: 400;
  font-size: 20px;
  line-height: 23px; }

.work {
  text-align: center; }
  .work__items {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    margin-top: 30px; }
  .work__item {
    position: relative;
    border: 1px solid #BDC3C7;
    width: 30%;
    padding: 30px;
    margin-bottom: 20px;
    color: #FFFFFF;
    font-weight: 500;
    font-size: 16px; }
    .work__item span {
      position: absolute;
      left: 5px;
      top: 5px;
      font-style: normal;
      font-weight: 500;
      font-size: 50px;
      line-height: 59px;
      color: #2C2C2C;
      backdrop-filter: blur(2px); }

.why_us {
  display: flex; }
  .why_us__left {
    width: 60%; }
  .why_us__text {
    font-weight: 400;
    font-size: 16px;
    line-height: 19px;
    color: #BDC3C7;
    margin-top: 100px;
    padding: 0 100px 0 50px; }
    .why_us__text:last-child {
      margin-top: 40px; }
  .why_us__right {
    width: 40%;
    position: relative; }
    .why_us__right img {
      width: 100%; }
  .why_us .experience {
    width: 305px;
    background: rgba(0, 0, 0, 0.3);
    backdrop-filter: blur(6px);
    padding: 30px 40px;
    position: absolute;
    left: -150px;
    top: 30px;
    display: flex;
    justify-content: space-between;
    align-items: center; }
    .why_us .experience__text-1 {
      font-weight: 500;
      font-size: 15px;
      line-height: 15px;
      color: #FFFFFF; }
      .why_us .experience__text-1 div {
        font-size: 30px; }
    .why_us .experience__text-2 {
      font-weight: 500;
      font-size: 30px;
      line-height: 35px;
      color: #D9CB9E; }
      .why_us .experience__text-2 div {
        font-size: 50px;
        line-height: 59px; }
  .why_us__text-2 {
    font-weight: 300;
    font-size: 25px;
    line-height: 29px;
    color: #D9CB9E;
    margin-top: 30px; }

.services {
  display: flex; }
  .services__left {
    width: 40%; }
    .services__left button {
      margin-top: 85px; }
  .services__right {
    width: 60%;
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap; }
  .services__title {
    font-weight: 300;
    font-size: 24px;
    line-height: 28px;
    color: #FFFFFF;
    margin-top: 40px; }
  .services__item {
    padding: 15px 20px;
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(6px);
    width: 30%;
    height: auto;
    margin-bottom: 3%; }
  .services__num {
    font-weight: 500;
    font-size: 30px;
    line-height: 35px;
    color: #374140; }

.partners_swiper {
  margin-top: 50px; }

.about__top {
  display: flex; }

.about__left {
  width: 50%; }
  .about__left img {
    width: 100%; }

.about__right {
  width: 50%;
  padding-left: 50px;
  font-weight: 300;
  font-size: 16px;
  line-height: 19px;
  color: #BDC3C7; }

.about_services {
  margin-top: 50px; }
  .about_services__item {
    padding: 20px;
    background: #000000;
    height: auto; }
    .about_services__item img {
      width: 100%;
      margin-bottom: 20px; }

.about__title {
  font-weight: 500;
  font-size: 24px;
  line-height: 28px;
  color: #FFFFFF;
  margin-bottom: 30px; }

.contacts__top {
  display: flex;
  align-items: center;
  margin-top: 30px; }
  .contacts__top address {
    width: 70%;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin-right: 50px; }
    .contacts__top address a {
      font-style: normal;
      font-weight: 700;
      font-size: 18px;
      line-height: 21px;
      color: #BDC3C7;
      display: flex;
      align-items: center;
      width: 50%;
      margin-bottom: 20px; }
